import { Component, OnInit } from '@angular/core';
import { BasePagePage } from 'src/app/shared/base-page/base-page.page';
import { LoadingController, NavController, AlertController } from '@ionic/angular';
import { BaseService } from 'src/app/services/base.service';
import { Constants, config } from 'src/app/data-model/constant.model';
import { Storage } from '@ionic/storage';
import { DownModel, AccessTokenModel } from 'src/app/data-model/base.model';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-machine-down',
  templateUrl: './machine-down.page.html',
  styleUrls: ['./machine-down.page.scss'],
})
export class MachineDownPage extends BasePagePage implements OnInit {

  checkBoxList: Array<any> = [];
  isIndeterminate: boolean;
  masterCheck: boolean;
  machineDownReasonModel: DownModel = new AccessTokenModel(); 
  arrDownReasons: Array<any> = [];
  downTime: Date;

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    private service: BaseService,
    private storage: Storage,
    private route: ActivatedRoute) {

    super(loadingCtrl, alertCtrl);

  }

  ngOnInit() {

    if (this.route.snapshot.data['special']) {

      config.log('strDownTime-----------------------: ', this.route.snapshot.data['special']);

      this.downTime = this.route.snapshot.data['special'];

    }

    this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

      this.machineDownReasonModel.accesstoken = accessToken;

      this.service.callWebService(Constants.k_GET_MACHINE_DOWN_REASON, this.machineDownReasonModel, (res) => {

        this.arrDownReasons = JSON.parse(res.data);
  
        config.log('arrDown: ', this.arrDownReasons);

        this.arrDownReasons.forEach(element => {

          let machine = {
            'name' : element.name,
            'value' : element.value
          }

          this.checkBoxList.push({ 'machine': machine, 'isChecked': false })
    
        });
  
        // while (this.arrDownReasons.length > 0)
        //   this.arrRow.push(this.arrDownReasons.splice(0, 2));
  
      });

    });

  }

  checkMaster() {
    setTimeout(() => {
      this.checkBoxList.forEach(obj => {
        obj.isChecked = this.masterCheck;
      });
    });
  }

  checkEvent() {
    const totalItems = this.checkBoxList.length;
    let checked = 0;
    this.checkBoxList.map(obj => {
      if (obj.isChecked) checked++;
    });
    if (checked > 0 && checked < totalItems) {
      //If even one item is checked but not all
      this.isIndeterminate = true;
      this.masterCheck = false;
    } else if (checked == totalItems) {
      //If all are checked
      this.masterCheck = true;
      this.isIndeterminate = false;
    } else {
      //If none is checked
      this.isIndeterminate = false;
      this.masterCheck = false;
    }
  }

  onSendReportClicked(e: Event) {

    this.navCtrl.navigateBack('/home/clock-in');

  }

}
